html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  font-family: 'Roboto', serif;
}

.container {
  width: 100% !important;
  max-width: 1200px;
  padding: 0 15px;
  height: 100%;
}

.row {
  height: 100%;
}

.flex {
  display: flex;
}

.pop-up {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #101112;
  z-index: 1000;
}

.pop-up .close {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
}

.pop-up .close img {
  width: 80px;
  height: 80px;
  opacity: 0.16;
}

.pop-up__usd {
  position: relative;
  top: -26px;
  right: 50px;
}

.pop-up__usd h2 {
  color: #fbb400;
  font-size: 80px;
  font-family: 'Oswald';
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
}

.pop-up__usd p {
  font-size: 25px;
  font-weight: 400;
  color: #fdfdfd;
  text-align: center;
  text-transform: uppercase;
}

.pop-up-1 {
  display: none;
  color: #ffffff;
}

.pop-up-1 .iphone {
  width: 640px;
  position: absolute;
  top: 30px;
  left: -200px;
}

.pop-up-1 span {
  color: #fdfdfd;
  font-weight: 500;
  text-transform: uppercase;
  display: inline-block;
  margin-bottom: 10px;
}

@media only screen and (min-height: 1080px) {
  .pop-up-1 span {
    font-size: 25px;
    margin-bottom: 20px;
  }
}

.pop-up-1 h2 {
  color: #2776b3;
  margin-bottom: 20px;
}

@media only screen and (min-height: 1080px) {
  .pop-up-1 h2 {
    margin-bottom: 60px;
    font-size: 45px;
  }
}

.pop-up-1 p {
  line-height: 1.3;
  font-weight: 400;
  margin-bottom: 20px;
}

@media only screen and (min-height: 1080px) {
  .pop-up-1 p {
    font-size: 24px;
    margin-bottom: 40px;
  }
}

.pop-up-1 ul li {
  line-height: 1.41;
  font-weight: 400;
  margin-bottom: 20px;
  position: relative;
}

@media only screen and (min-height: 1080px) {
  .pop-up-1 ul li {
    font-size: 22px;
  }
}

.pop-up-1 ul li:before {
  content: '\f00c';
  position: absolute;
  font: normal normal normal 14px/1 FontAwesome;
  left: -30px;
  top: 0px;
  font-size: 20px;
  color: #2776b3;
}

.pop-up-1 ul li span {
  color: #2776b3;
  text-transform: none;
  font-weight: 400;
  margin-bottom: 0;
}

.pop-up-2 {
  padding-top: 200px;
  display: none;
  background-image: linear-gradient(107deg, #023b92 0%, #0189c1 46%, #023b92 100%);
}

.pop-up-2 ul li {
  color: #ffffff;
  font-size: 22px;
  font-weight: 500;
  line-height: 1.41;
  font-weight: 400;
  margin-bottom: 20px;
  position: relative;
}

.pop-up-2 ul li:before {
  content: '\f1ce';
  position: absolute;
  font: normal normal normal 14px/1 FontAwesome;
  left: -30px;
  top: 5px;
  font-size: 20px;
  color: #fbb400;
}

.pop-up-3 {
  background: rgba(0, 0, 0, 0.8);
  display: none;
}

.pop-up-3 .close {
  width: 50px;
  height: 50px;
  border: 2px solid #eeeeee;
  background: #353434;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -20px;
  right: -20px;
}

.pop-up-3 .close span {
  font-size: 20px;
  color: #eeeeee;
}

.pop-up-3 .pop__block {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #eeeeee;
  box-shadow: 1px 1.7px 12.4px 0.6px rgba(0, 0, 0, 0.21);
  padding: 20px;
}

.pop-up-3 .pop__block__photo {
  width: 260px;
  height: 260px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 20px;
  border: solid 3px #2776b3;
  border-radius: 50%;
}

.pop-up-3 .pop__block__photo img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.pop-up-3 .pop__block__name h2 {
  font-size: 28px;
  font-weight: 500;
  color: #353434;
}

.pop-up-3 .pop__block__name span {
  font-size: 25px;
  font-weight: 300;
  color: #2776b3;
}

.pop-up-3 .pop__block__spec p {
  font-size: 20px;
  color: #353434;
}

.pop-up-3 .pop__block hr {
  border: solid 4px #2776b3;
  width: 40px;
}

.pop-up-3 .pop__block__desc p {
  font-size: 16px;
  line-height: 1.35;
  text-align: center;
  color: #353434;
  margin-bottom: 20px;
}

.pop-up-3 .pop__block__social {
  display: flex;
  justify-content: center;
}

.pop-up-3 .pop__block__social a {
  width: 40px;
  height: 40px;
  color: #ffffff;
  background: #2776b3;
  border-radius: 50%;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all 0.3s;
}

.pop-up-3 .pop__block__social a:hover {
  background: #ffffff;
  color: #2776b3;
}
