@import "helpers/all";

body {
	font-family: 'Roboto', serif; }

.container {
	width: 100% !important;
	max-width: 1200px;
	padding: 0 15px;
	height: 100%; }
.row {
	height: 100%; }


.flex {
	display: flex; }


.pop-up {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #101112;
	z-index: 1000;
	.close {
		position: absolute;
		right: 20px;
		top: 20px;
		cursor: pointer;
		img {
			width: 80px;
			height: 80px;
			opacity: 0.16; } }
	&__usd {
		position: relative;
		top: -26px;
		right: 50px;
		h2 {
			color: $yellow-orange;
			font-size: 80px;
			font-family: 'Oswald';
			font-weight: normal;
			text-align: center;
			text-transform: uppercase; }
		p {
			font-size: 25px;
			font-weight: 400;
			color: #fdfdfd;
			text-align: center;
			text-transform: uppercase; } }
	&-1 {
		display: none;
		color: #ffffff;
		.iphone {
			width: 640px;
			position: absolute;
			top: 30px;
			left: -200px; }
		span {
			color: #fdfdfd;
			font-weight: 500;
			text-transform: uppercase;
			display: inline-block;
			margin-bottom: 10px;
			@include rhmin(1080) {
				font-size: 25px;
				margin-bottom: 20px; } }
		h2 {
			color: $bluish;
			margin-bottom: 20px;
			@include rhmin(1080) {
				margin-bottom: 60px;
				font-size: 45px; } }
		p {
			line-height: 1.3;
			font-weight: 400;
			margin-bottom: 20px;
			@include rhmin(1080) {
				font-size: 24px;
				margin-bottom: 40px; } }
		ul {
			li {
				line-height: 1.41;
				font-weight: 400;
				margin-bottom: 20px;
				position: relative;
				@include rhmin(1080) {
					font-size: 22px; }
				&:before {
					content: '\f00c';
					position: absolute;
					font: normal normal normal 14px/1 FontAwesome;
					left: -30px;
					top: 0px;
					font-size: 20px;
					color: $bluish; }
				span {
					color: $bluish;
					text-transform: none;
					font-weight: 400;
					margin-bottom: 0; } } } }



	&-2 {
		padding-top: 200px;
		display: none;
		background-image: linear-gradient( 107deg, rgb(2,59,146) 0%, rgb(1,137,193) 46%, rgb(2,59,146) 100%);
		ul {
			li {
				color: #ffffff;
				font-size: 22px;
				font-weight: 500;
				line-height: 1.41;
				font-weight: 400;
				margin-bottom: 20px;
				position: relative;
				&:before {
					content: '\f1ce';
					position: absolute;
					font: normal normal normal 14px/1 FontAwesome;
					left: -30px;
					top: 5px;
					font-size: 20px;
					color: $yellow-orange; } } } }
	&-3 {
		background: rgba($black-two, 0.8);
		display: none;
		.close {
			width: 50px;
			height: 50px;
			border: 2px solid #eeeeee;
			background: $black-three;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: -20px;
			right: -20px;
			span {
				font-size: 20px;
				color: #eeeeee; } }
		.pop {
			&__block {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				background-color: #eeeeee;
				box-shadow: 1px 1.7px 12.4px 0.6px rgba(0, 0, 0, 0.21);
				padding: 20px;
				&__photo {
					width: 260px;
					height: 260px;
					position: relative;
					left: 50%;
					transform: translateX(-50%);
					margin-bottom: 20px;
					border: solid 3px $bluish;
					border-radius: 50%;
					img {
						width: 100%;
						height: 100%;
						border-radius: 50%; } }
				&__name {
					h2 {
						font-size: 28px;
						font-weight: 500;
						color: $black-three; }
					span {
						font-size: 25px;
						font-weight: 300;
						color: $bluish; } }
				&__spec {
					p {
						font-size: 20px;
						color: $black-three; } }
				hr {
					border: solid 4px $bluish;
					width: 40px; }
				&__desc {
					p {
						font-size: 16px;
						line-height: 1.35;
						text-align: center;
						color: $black-three;
						margin-bottom: 20px; } }
				&__social {
					display: flex;
					justify-content: center;
					a {
						width: 40px;
						height: 40px;
						color: #ffffff;
						background: $bluish;
						border-radius: 50%;
						margin-right: 10px;
						display: flex;
						justify-content: center;
						align-items: center;
						text-decoration: none;
						transition: all 0.3s;
						&:hover {
							background: #ffffff;
							color: $bluish; } } } } } } }

